export default {
	data() {
		return {
			tuishuList:[],
			temp: [],
			InputBottom: 0
		}
	},
	onLoad() {
		var me = this;
		uni.request({
			url: "http://134.175.204.38:66/tuishu/getTuiShu",
			method: "GET",
			success(res) {
				me.temp = [];
				var temp = res.data.data;
				me.temp = temp;
				console.log(temp);
			}
		})
		uni.request({
			url: "http://134.175.204.38:66/tuishu/getTuiShu",
			method: "GET",
			success(res) {
				me.tuishuList = [];
				var tuishuList = res.data.data;
				me.tuishuList = tuishuList;
				console.log(tuishuList);
			}
		})
	},
	methods: {
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		}
	}
}
